<template>
<div id="app">
  <header class="header">
    <el-row>
      <el-col :span="24">
        <el-menu theme="dark" default-active="/yun_ying_shang" class="el-menu-demo" mode="horizontal" @select="selectedIndex" :router="true">
          <el-menu-item index="/operator_pro" :class="{'isActive': active}">运营商数据</el-menu-item>
          <el-menu-item index="/ecommerce" :class="{'isActive': !active}">电商数据</el-menu-item>
          <el-menu-item index="/wechat" :class="{'isActive': !active}">微信数据</el-menu-item>
          <el-menu-item index="/jd" :class="{'isActive': !active}">京东数据</el-menu-item>
        </el-menu>
      </el-col>
    </el-row>
  </header>

  <main>
    <div class="main-right">
      <router-view class="view"></router-view>
    </div>
  </main>
</div>
</template>

<script>

export default {
  name: 'app'
}
</script>

<style>
body {
  margin: 0;
}

#app {
  min-width: 1200px;
  margin: 0 auto;
  font-family: "Helvetica Neue", "PingFang SC", Arial, sans-serif;
}

/* 头部导航 */

header {
  z-index: 1000;
  min-width: 1200px;
  transition: all 0.5s ease;
  border-top: solid 0px #3091F2;
  background-color: #fff;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04);
}

header.header-fixed {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

header .el-menu-demo {
  padding-left: 0px!important;
}

/* 主内容区 */

main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  min-height: 800px;
  border: solid 40px #E9ECF1;
  background-color: #FCFCFC;
}

main .main-right {
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  background-color: #fff;
  padding: 50px 70px;
}

main .main-right .el-input {
  width: 300px;
}

main .el-menu {
  background-color: transparent!important;
}
</style>



