<template>
  <div class="yys_call_sheet">
    <el-table :data="tableData" style="width: 100%">
      <el-table-column type="expand">
        <template scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="姓名">
              <span>{{ props.row.name }}</span>
            </el-form-item>
            <el-form-item label="手机号">
              <span>{{ props.row.telNum }}</span>
            </el-form-item>
            <el-form-item label="通话号码">
              <span>{{ props.row.otherTelNum }}</span>
            </el-form-item>
            <el-form-item label="呼叫类型">
              <span>{{ props.row.callType }}</span>
            </el-form-item>
            <el-form-item label="通话地点">
              <span>{{ props.row.callLocation }}</span>
            </el-form-item>
            <el-form-item label="通话时长">
              <span>{{ props.row.callDuration }}</span>
            </el-form-item>
            <el-form-item label="费用">
              <span>{{ props.row.fee }}</span>
            </el-form-item>
            <el-form-item label="通话时间">
              <span>{{ props.row.callStartTime }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column type="index" label="序号" width="100">
      </el-table-column>
      <el-table-column prop="name" label="姓名" width="150">
      </el-table-column>
      <el-table-column prop="telNum" label="手机号码" width="200">
      </el-table-column>
      <el-table-column prop="otherTelNum" label="通话号码" width="200">
      </el-table-column>
      <el-table-column prop="callLocation" label="通话地点" width="200" sortable>
      </el-table-column>
      <el-table-column prop="callStartTime" label="通话时间" width="200" sortable>
      </el-table-column>
      <el-table-column label="操作">
        <template scope="scope">
          <el-button type="text" size="small">下载</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>

  export default {
    name: 'yys_call_sheet',
    data: function () {
      return {
        tableData: [
          {
            name: '丢你雷姆',
            telNum: '17816875411',
            otherTelNum: '10086',
            callType: '主叫',
            callLocation: '杭州',
            callDuration: '1000',
            fee: '0.50',
            callStartTime: '2017-10-11 12:00:00',
          },
          {
            name: '尧俊',
            telNum: '123456789',
            otherTelNum: '012345678',
            callType: '被叫',
            callLocation: '上海',
            callDuration: '10000',
            fee: '1.50',
            callStartTime: '2017-10-17 12:00:00',
          }
        ]
      }
    }
  }
</script>

<style>

</style>



