<template>
  <div id="ecommerce">
    <el-form>
      <el-row :gutter="20">
        <el-col :span="6">
          <el-form-item>
            <el-input v-model="name" placeholder="请输入内容">
              <template slot="prepend">姓名</template>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item style="margin-left: 50px;">
            <el-input v-model="identityCard" placeholder="请输入内容">
              <template slot="prepend">身份证号</template>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item style="margin-left: 100px;">
            <el-input v-model="telNum" placeholder="请输入内容">
              <template slot="prepend">手机号码</template>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <!--<el-select v-model="value" placeholder="请选择">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>-->
          <el-button type="primary" icon="search" style="float:right;" @click="handleSearch">搜索</el-button>
        </el-col>
      </el-row>
    </el-form>

    <router-view class="view"></router-view>

  </div>
</template>

<script>
  import eventHub from '../../assets/eventHub.js';

  export default {
    name: 'ecommerce',
    data: function () {
      return {
        name: '',
        identityCard: '',
        telNum: '',
      }
    },
    methods: {
      handleSearch: function () {
        this.$store.state.searchName = this.name;
        this.$store.state.searchIdCard = this.identityCard;
        this.$store.state.searchTelNum = this.telNum;
        this.$store.state.searchAuthItem = 'ecommerce';
        if (this.name == "" && this.identityCard == "" && this.telNum == "") {
          this.$message({
            showClose: true,
            message: '姓名、身份证号和手机号不能为空',
            type: 'warning'
          });
          return;
        }
        this.$router.push({
          path: '/ecommerce_result',
          query: {name: this.name, identityCard: this.identityCard, telNum: this.telNum, authItem: 'ecommerce'}
        });
        eventHub.$emit('searchEcommerceData', '');
      }
    }
  }
</script>


<style>
  .demo-table-expand {
    font-size: 0;
  }

  .demo-table-expand label {
    width: 100px;
    color: #99a9bf;
  }

  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 33.3%;
  }
</style>



