<template>
  <div class="yys_bill_info">
    <el-table :data="tableData" v-loading="loading" element-loading-text="玩命加载中" style="width: 100%">
      <el-table-column type="expand">
        <template scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="姓名">
              <span>{{ props.row.name }}</span>
            </el-form-item>
            <el-form-item label="手机号码">
              <span>{{ props.row.telNum }}</span>
            </el-form-item>
            <el-form-item label="月基本费">
              <span>{{ props.row.baseFee }}</span>
            </el-form-item>
            <el-form-item label="语音费">
              <span>{{ props.row.voiceCallFee }}</span>
            </el-form-item>
            <el-form-item label="上网费">
              <span>{{ props.row.networkFee }}</span>
            </el-form-item>
            <el-form-item label="实际应缴费用">
              <span>{{ props.row.payFee }}</span>
            </el-form-item>
            <el-form-item label="消费合计">
              <span>{{ props.row.totalFee }}</span>
            </el-form-item>
            <el-form-item label="账单月份">
              <span>{{ props.row.billMonth }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column type="index" label="序号" width="100">
      </el-table-column>
      <el-table-column prop="name" label="姓名" width="150">
      </el-table-column>
      <el-table-column prop="telNum" label="手机号码" width="200">
      </el-table-column>
      <el-table-column prop="baseFee" label="月基本费" width="200" sortable>
      </el-table-column>
      <el-table-column prop="totalFee" label="消费合计" width="200" sortable>
      </el-table-column>
      <el-table-column prop="billMonth" label="账单月份" width="200" sortable>
      </el-table-column>
      <el-table-column label="操作">
        <template scope="scope">
          <el-button type="text" size="small">下载</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import eventHub from '../../assets/eventHub';
  export default {
    name: 'yys_bill_info',
    data: function () {
      return {
        tableData: [],
        loading: false,
      }
    },
    created: function () {
      var _this = this;
      _this.getData();
      eventHub.$on('searchBillInfoData', () => {
          _this.getData();
      });
    },
    methods: {
        getData:function(){
          this.loading = true;
          this.$http.get('/api/login1',
            {
              params: {
                name: this.$store.state.searchName,
                identityCard: this.$store.state.searchIdCard,
                telNum: this.$store.state.searchTelNum,
              }
            }).then(
            res => {
              this.loading = false;
              var data = [];
              for (let i = 0; i < res.data.length; i++) {
                var obj = {};
                obj.name = res.data[i].name;
                obj.telNum = res.data[i].cupSize;
                obj.baseFee = res.data[i].age;
                data[i] = obj;
              }
              this.tableData = data;
            },
            err => {
              this.$alert(err.message);
            });
        }
    },
    beforeDestroy: function() {
      eventHub.$off('searchBillInfoData');
    }
  }
</script>

<style>

</style>



