    <el-table-column prop="name" label="姓名" width="150">
    </el-table-column>
    <el-table-column prop="telNum" label="手机号码" width="200">
    </el-table-column>
    <el-table-column prop="otherTelNum" label="对方号码" width="200">
    </el-table-column>
    <el-table-column prop="smsTime" label="发送／接收时间" width="350" sortable>
    </el-table-column>
    <el-table-column label="操作">
      <template scope="scope">
        <el-button type="text" size="small">下载</el-button>
      </template>
    </el-table-column>
  </el-table>
</div>
</template>

<script>

export default {
  name: 'yys_sms_sheet',
  data: function() {
    return {
      tableData: [{
        name: '丢你雷姆',
        telNum: '17816875411',
        otherTelNum: '10086',
        smsType: '发送',
        fee: '0.50',
        smsTime: '2017-10-11 12:00:00',
      }]
    }
  }
}
</script>

