<template>
  <div id="operator_pro">
    <el-form>
      <el-row :gutter="20">
        <el-col :span="6">
          <el-form-item style="float:left;">
            <el-input v-model="telNum" placeholder="请输入内容">
              <template slot="prepend">手机号码</template>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="18">
          <!--<el-select v-model="value" placeholder="请选择">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>-->
          <el-button type="primary" icon="search" style="float:right;" @click="handleSearch">搜索</el-button>
        </el-col>
      </el-row>
    </el-form>

    <router-view class="view"></router-view>

  </div>
</template>

<script>
  import eventHub from '../../assets/eventHub.js';

  export default {
    name: 'operator_pro',
    data: function () {
      return {
        telNum: ''
      }
    },
    methods: {
      handleSearch: function () {
        this.$store.state.searchTelNum = this.telNum;
        this.$store.state.searchAuthItem = 'operator_pro';
        if (this.telNum == "") {
          this.$message({
            showClose: true,
            message: '手机号不能为空',
            type: 'warning'
          });
          return;
        }
        this.$router.push({
          path: '/operator_pro_result',
          query: {telNum: this.telNum, authItem: 'operator_pro'}
        });
        eventHub.$emit('searchOperatorProData', '');
      }
    }
  }
</script>


<style>
  .demo-table-expand {
    font-size: 0;
  }

  .demo-table-expand label {
    width: 100px;
    color: #99a9bf;
  }

  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



