<template>
  <div id="wechat">
    <el-form :data="tableData">
      <el-row :gutter="20">
        <el-col :span="6">
          <el-form-item style="float:left;">
            <el-input v-model="telNum" placeholder="请输入内容">
              <template slot="prepend">手机号码</template>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="18">
          <el-button type="primary" icon="search" style="float:right;" @click="handleSearch">搜索</el-button>
        </el-col>
      </el-row>
    </el-form>

    <router-view class="view"></router-view>

  </div>
</template>

<script>

  export default {
    name: 'wechat',
    data: function () {
      return {
        telNum: ''
      }
    },
    methods: {
      handleSearch: function () {
        this.$store.state.searchTelNum = this.telNum;
        this.$store.state.searchAuthItem = 'wechat';
        if (this.telNum == "") {
          this.$message({
            showClose: true,
            message: '手机号不能为空',
            type: 'warning'
          });
          return;
        }
        this.$router.push({
          path: '/wechat_result',
          query: {telNum: this.telNum, authItem: 'wechat'}
        });
        eventHub.$emit('searchWechatData', '');
      }
    }
  }
</script>

<style>

</style>



