import Vue from 'vue'
import Router from 'vue-router'

import operator_pro from '@/views/operator_pro'
import operator_pro_result from '@/views/operator_pro/result'
import yys_bill_info from '@/views/operator_pro/bill_info'
import yys_call_sheet from '@/views/operator_pro/call_sheet'
import yys_sms_sheet from '@/views/operator_pro/sms_sheet'
import yys_familiarity_num from '@/views/operator_pro/familiarity_num'

import ecommerce from '@/views/ecommerce'
import ecommerce_result from '@/views/ecommerce/result'

import wechat from '@/views/wechat'
import wechat_result from '@/views/wechat/result'


import Vuex from 'vuex'

Vue.use(Router)
Vue.use(Vuex)


export default new Router({
  routes: [{
      path: '/',
      name: '默认页',
      component: app
    },
    {
      path: '/operator_pro',
      name: '运营商',
      component: operator_pro,
      children: [
        {
          path: '/operator_pro_result',
          name: '运营商基本信息',
          component: operator_pro_result
        },
        {
          path: '/operator_pro/bill_info',
          name: '运营商账单信息',
          component: yys_bill_info
        },
        {
          path: '/operator_pro/call_sheet',
          name: '运营商通话记录',
          component: yys_call_sheet
        },
        {
          path: '/operator_pro/sms_sheet',
          name: '运营商短信记录',
          component: yys_sms_sheet
        },
        {
          path: '/operator_pro/familiarity_num',
          name: '运营商亲情网',
          component: yys_familiarity_num
        }
      ]
    },
    {
      path: '/wechat',
      name: '默认页',
      component: wechat,
      children: [
        {
          path: '/wechat_result',
          name: '微信基本信息',
          component: wechat_result
        }
      ]
    },
    {
      path: '/ecommerce',
      name: '电商数据',
      component: ecommerce,
      children: [
        {
          path: '/ecommerce_result',
          name: '电商基本信息',
          component: ecommerce_result
        }
      ]
    },
  ]
})



