<template>
  <div class="operator_pro_result">
    <el-table :data="tableData" v-loading="loading" style="width: 100%">
      <el-table-column type="expand">
        <template scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="姓名">
              <span>{{ props.row.name }}</span>
            </el-form-item>
            <el-form-item label="身份证号">
              <span>{{ props.row.identityCard }}</span>
            </el-form-item>
            <el-form-item label="手机号码">
              <span>{{ props.row.telNum }}</span>
            </el-form-item>
            <el-form-item label="手机开通省份">
              <span>{{ props.row.telNumProvince }}</span>
            </el-form-item>
            <el-form-item label="用户状态">
              <span>{{ props.row.telNumStatus }}</span>
            </el-form-item>
            <el-form-item label="账户余额">
              <span>{{ props.row.accountBalance }}</span>
            </el-form-item>
            <el-form-item label="查询时间">
              <span>{{ props.row.createDate }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column type="index" label="序号" width="100">
      </el-table-column>
      <el-table-column prop="name" label="姓名" width="150">
      </el-table-column>
      <el-table-column prop="identityCard" label="身份证号" width="200">
      </el-table-column>
      <el-table-column prop="telNum" label="手机号码" width="200">
      </el-table-column>
      <el-table-column prop="accountBalance" label="账户余额" width="200" sortable>
      </el-table-column>
      <el-table-column prop="createDate" label="查询时间" width="200" sortable>
      </el-table-column>
      <el-table-column label="操作">
        <template scope="scope">
          <el-button type="text" size="small">下载</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- <el-col :span="24" class="toolbar">
      <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="1" :total="total" style="float:right;">
      </el-pagination>
    </el-col>-->
  </div>

</template>

<script>
  import eventHub from '../../assets/eventHub';
  export default {
    name: 'operator_pro_result',
    data: function () {
      return {
        tableData: [],
        loading: false,
      }
    },
    created: function () {
      var _this = this;
      _this.getData();
      eventHub.$on('searchOperatorProData', () => {
        _this.getData();
      });
    },
    methods: {
      getData: function () {
        this.loading = true;
        this.$http.get('http://192.168.1.121:8080/getOperatorProData',
          {
            params: {
              telNum: this.$store.state.searchTelNum,
              authItem: this.$store.state.searchAuthItem,
            }
          }).then(
          res => {
            this.loading = false;
            var data = [];
            for (let i = 0; i < res.data.length; i++) {
              var obj = {};
              obj.name = res.data[i].baseInfo.name;
              obj.telNum = res.data[i].baseInfo.mobile;
              obj.baseFee = res.data[i].age;
              data[i] = obj;
            }
            this.tableData = data;
          },
          err => {
            this.$alert(err.message);
          });
      }
    },
    beforeDestroy: function () {
      eventHub.$off('searchOperatorProData');
    }
  }
</script>

<style>

</style>



